class Skills extends React.Component {

	constructor(props) {
		super(props)
	}
	
	render () {
		return (
			<section id="skills" className="skills" ref={(section) => this.Skills = section} onClick={this.scroll}>
				<div className='wrap'>
					<h3 className="skills__header">{this.props.title}</h3>
					<ul className="skills__grid">
						{this.props.skills.map((skill, i) => {
							return (
								<li key={i} className="skill">
									<div className='skill__image'>
										<img src={skill.image} alt={skill.name} />
									</div>
									<p className="skill__name">{skill.name}</p>
								</li>
							)
						})}
					</ul>
				</div>
			</section>
		)
	}
}

export default Skills
